import { LOAD_CARDS, LOAD_PHOTOS, SHOW, HIDE } from './types';

export const photoLoader = (payload:any) => {
    return {
        type:LOAD_PHOTOS,
        payload
    }
}

export const cardsLoader = (payload:any) => {
    return {
        type:LOAD_CARDS,
        payload
    }
}

export const showNav = () => {
    return {type:SHOW}
}

export const hideNav = () => {
    return {type:HIDE}
}